@import '../../helpers/mixins.scss';

.Footer {
  display: flex;
  justify-Content: space-around;
  align-items: center;
  height: 96px;
  border-top: 1px solid #E2E6E9;
  margin-top: auto;

  @include onPhone {
    flex-direction: column;
    height: auto;
    gap: 32px;
    padding: 32px 16px;
    align-items: normal;
  }

  &__center {
    display: flex;
    list-style: none;
    text-transform: uppercase;

    ul {
      display: flex;
      gap: 100px;
      list-style: none;

      @include onTablet {
        gap: 13px;
      }
  
      @include onPhone {
        flex-direction: column;
        gap: 16px;
      }
    }

    &-item {
      a {
        color: #89939A;
        text-decoration: none;

        &:hover {
          color: #313237;
        }
      }
    }
  }

  &__rightContainer {
    display: flex;
    justify-content: center;
    align-items: center;

    .ArrowUp {
      height: 32px;
      width: 32px;
      margin-left: 16px;
      border: 1px solid #B4BDC3;
      background-color: white;

      display: flex;
      justify-content: center;
      align-items: center;

      &:hover {
        border: 1px solid #313237
      }
    }
  }
}

